import './main.scss';
import {Link} from 'react-router-dom';
import Avatar from '../assets/Images/av.jpg';


const Settings = () => {
   return (
      <div className="settings">

         <form className="settings-inner">
            <div className="settings-header">
               <label htmlFor="avatar"><img src={Avatar} alt="" /> </label>
               <h1>Asadulloh</h1>
               <input type="file" id="avatar"/>
            </div>

            
            <div className="row">
               <label htmlFor="setfullName">Full name</label>
               <input type="text" placeholder="Robert" id="setfullName" name="fullName"/>
            </div>

            <div className="row">
               <label htmlFor="setemail">Email</label>
               <input type="email" name="email" id="setemail" placeholder="robert@gmail.com"/>
            </div>

            <div className="row">
               <label htmlFor="setphoneNumber">Phone Number</label>
               <input type="tel" id="setphoneNumber" name="phoneNumber"/>
            </div>

            <div className="row">
               <label htmlFor="settruckNumber">Truck number</label>
               <input type="number" id="settruckNumber" name="truckNumber"/>
            </div>

            <div className="row">
               <label htmlFor="settruckId">Truck ID</label>
               <input type="number" id="settruckId" name="truckId"/>
            </div>

            {/* <div className="row">
               <label htmlFor="password">Password</label>
               <input type="password" id="password" name="password"/>
            </div>

            <div className="row">
               <label htmlFor="passwordConf">Password Confirmation</label>
               <input type="password" id="passwordConf" name="passwordConf"/>
            </div> */}

            <div className="row">
               <button>Save</button>
            </div>
         </form>
      </div>
   )
}

export default Settings;