import './UserBlock.scss';
import image from '../../assets/Images/user.svg';
import {Link} from 'react-router-dom';

const UserBlock = () => {
   return(
      <Link className="userBlock" to="/Userinfo">
         <div className="userBlock-header">
            <img src={image} alt="" />
            <div className="userBlock-info">
               <h1>User name</h1>
               <span>ID: 32564</span>
            </div>
         </div>

         <div className="row">
            <span>21:00</span>
         </div>
      </Link>
   )
}

export default UserBlock;