import './main.scss';
import {Link} from 'react-router-dom';
import UserBlock from '../components/UserBlock/UserBlock';
import Settings from './Settings';

const UserPages = (a) => {
   switch (a) {
      case "first":
         return (
            <div className="b">
               <Settings/>
            </div>
         );
         break;
      case "second":
         return (
            <form className="user-inner">
               <div className="user-header">
                  <h2>User</h2>
               </div>

               <div className="row">
                  <label htmlFor="price">Texnik xolat uchun harajat</label>
                  <input type="number" name="" id="price" />
               </div>

               <div className="row">
                  <label htmlFor="fuel">Yoqilg'i</label>
                  <input type="number" name="" id="fuel" />
               </div>

               <div className="row">
                  <label htmlFor="oils">Moy necha kilometrda almashtirdi</label>
                  <input type="number" name="" id="oils" />
               </div>

               <div className="row">
                  <label htmlFor="oilsPrice">Moy almashtirganda harajat</label>
                  <input type="number" name="" id="oilsPrice" />
               </div>

               <div className="row">
                  <label htmlFor="load">Yuk narxi</label>
                  <input type="number" name="" id="load" />
               </div>

               <div className="row">
                  <Link to="/Admin">Send</Link>
               </div>
            </form>
         );
         break;
      default:
         return (
            <div className="main">
               <UserBlock/>
               <UserBlock/>
               <UserBlock/>
               <UserBlock/>
               <UserBlock/>
               <UserBlock/>
               <UserBlock/>
            </div>
         );
         break;
   }
}

export default UserPages;