import './main.scss';
import {Link} from 'react-router-dom'


const SignUp = () => {
   return (
      <div className="signUp">

         <form className="signUp-inner">
            <div className="signUp-header">
               <h1>Create your account</h1>
            </div>

            
            <div className="row">
               <label htmlFor="fullName">Full name</label>
               <input type="text" placeholder="Robert" id="fullName" name="fullName"/>
            </div>

            <div className="row">
               <label htmlFor="email">Email</label>
               <input type="email" name="email" id="email" placeholder="robert@gmail.com"/>
            </div>

            <div className="row">
               <label htmlFor="phoneNumber">Phone Number</label>
               <input type="tel" id="phoneNumber" name="phoneNumber"/>
            </div>

            <div className="row">
               <label htmlFor="truckNumber">Truck number</label>
               <input type="number" id="truckNumber" name="truckNumber"/>
            </div>

            <div className="row">
               <label htmlFor="truckId">Truck ID</label>
               <input type="number" id="truckId" name="truckId"/>
            </div>

            <div className="row">
               <label htmlFor="password">Password</label>
               <input type="password" id="password" name="password"/>
            </div>

            <div className="row">
               <label htmlFor="passwordConf">Password Confirmation</label>
               <input type="password" id="passwordConf" name="passwordConf"/>
            </div>

            <div className="row">
               <Link to="/Login">Sign up</Link>
            </div>
         </form>
      </div>
   )
}

export default SignUp;