import './main.scss';
import {Link} from 'react-router-dom'
import { useRef, useState } from 'react';
import request from '../services';

const Login = ({ setToken, token }) => {

   const [hasError, setHasError] = useState(false)
   const emailInputRef = useRef(null)
   const passwordInputRef = useRef(null)

   const handleSubmit = (e) => {
      e.preventDefault();
      request.post('/login', {
         email: emailInputRef.current.value,
         password: passwordInputRef.current.value,
      })
         .then((response) => {
            window.localStorage.setItem('sessionToken', response.data.token)
            setToken(response.data.token)
            window.location.assign('/User')

         })
         .catch((err) => {
            window.location.assign('/User')
            setHasError(true)
         })
   }
   return(
      <div className="login">
         <form className="login-inner" onSubmit={handleSubmit}>
            <div className="login-header">
               <h1>Your account</h1>
            </div>

            <div className="row">
               <label htmlFor="loginEmail">Email</label>
               <input ref={emailInputRef} type="email" name="email" id="loginEmail" />
            </div>

            <div className="row">
               <label htmlFor="loginPassword">Password</label>
               <input ref={passwordInputRef}  type="password" id="loginPassword" name="password" />
            </div>

            <div className="row">
               <button type="submit" >Login</button>
               <Link to="/Signup">Sign up</Link>
            </div>
         </form>
      </div>
   )
}

export default Login;