import './main.scss';
import image from '../assets/Images/user.svg';

const UserInfo = () => {
   return(
      <div className="userInfo container">
         <div className="userInfo-header">
            <div className="userInfo-header-left">
               <img src={image} alt="" />

               <div className="userInfo-title">
                  <h2>User name</h2>
                  <span>ID: 121645</span>
               </div>
            </div>
            <div className="userInfo-header-right">
               <span>21:00</span>
            </div>
         </div>

         <div className="userInfo-info">
            <div className="row">
               <h3>Texnik holat uchun harajat :</h3>
               <span>25$</span>
            </div>
            <div className="row">
               <h3>Yoqilg'i</h3>
               <span>75 l</span>
            </div>
            <div className="row">
               <h3>Moyni necha kilometrda almashtirdi :</h3>
               <span>1000 km</span>
            </div>
            <div className="row">
               <h3>Moy almashtirganda harajat :</h3>
               <span>55$</span>
            </div>
            <div className="row">
               <h3>Yuk narxi :</h3>
               <span>5500$</span>
            </div>
         </div>
      </div>
   )
}

export default UserInfo;