import './main.scss';
import UserPages from '../innerPages/UserPages';
import {useState} from 'react';
import Main from '../assets/Images/home.svg';
import Plus from '../assets/Images/plus.svg';
import Settings from '../assets/Images/settings.svg';

const User = () => {
   const [page, setPage] = useState("")
   return(
      <div className="user">
         
         <div className="user-sidebar">
            <button onClick={() => setPage("")} className={`section-text ${page == "" ? "active" : ""} `}> <img src={Main} alt="" /> </button>
            <button onClick={() => setPage("second")} className={`section-text ${page == "second" ? "active" : ""}`}> <img src={Plus} alt="" /> </button>
            <button onClick={() => setPage("first")} className={`section-text ${page == "first" ? "active" : ""}`}> <img src={Settings} alt="" /> </button>
         </div>
         <div className="user-body">
            {UserPages(page)}
         </div>

         
      </div>
   )
}

export default User;